import axios from "axios";
import config from "./config";
import router from "@/router";
import { ElMessage } from "element-plus";
import i18n from "@/i18n";

const { t } = i18n.global;

export default function request(options) {
    return new Promise((resolve, reject) => {
        const instance = axios.create({ ...config })
        // 
        instance.interceptors.request.use(
            (config) => {
                let token = localStorage.getItem("pm_token");
                if (token) {
                    config.headers.token = token;
                } else {
                    router.push("/login")
                }

                return config;
            },
            (error) => {
                console.log("request: ", error);
                if (error.code === "ECONNABORTED" && error.message.indexOf("timeout") !== -1) {
                    ElMessage({message: '', type: 'error', showClose: true});
                }
                return Promise.reject(error);
            }
        );

        // 
        instance.interceptors.response.use(
            (response) => {
                return response.data;
            },

            (err) => {
                if (err && err.response) {
                    switch (err.response.status) {
                        case 400:
                            err.message = "";
                            break;
                        case 401:
                            err.message = "";
                            break;
                        case 403:
                            err.message = "";
                            break;
                        case 404:
                            err.message = " ${err.response.config.url}";
                            break;
                        case 408:
                            err.message = "";
                            break;
                        case 500:
                            err.message = "";
                            break;
                        case 501:
                            err.message = "";
                            break;
                        case 502:
                            err.message = "";
                            break;
                        case 503:
                            err.message = "";
                            break;
                        case 504:
                            err.message = "";
                            break;
                        case 505:
                            err.message = "HTTP";
                            break;
                        default: 
                    }
                }
                console.error(err);
                if (err.message) {
                    // 
                    // ElMessage({message: t(err.message), type: "error", showClose: true});
                    ElMessage({message: err.message, type: "error", showClose: true});
                }

                return Promise.reject(err);
            }
        );

        // 
        instance(options)
        .then((res) => {
            /**
             * 
             * 
             * response 
             * {
             *    code:200
             *    msg:'[string]',
             *    data:'[Any]'
             * }
             * code :
             * 200 
             * -1 
             * -2 
             */

            if (res.code === 200) {
                resolve(res);
            } else {
                if (res.code === -2) {
                    router.push("/login");
                }
                // TODO: 
                // ElMessage({message: t(err.message), type: "error", showClose: true});
                ElMessage({message: res.msg || "", type: "error", showClose: true});
                reject(res);
            }
        })
        .catch((error) => {
            reject(error);
        });
    });
}